import cl from './AdminTableHeader.module.css';

export default function AdminTableHeader() {

    return (
        <div className={cl.header}>
            <div className={cl['index-cell']}></div>
            <div className={cl.collapsed}>/</div>
            <div className={cl['name-cell']}></div>
            <div className={cl['inst-cell']}></div>
            <div className={cl['tel-cell']}></div>
            <div className={cl['date-cell']}></div>
        </div>
    );
}