export function convertResponseCode(code) {
    let message = '-   ';
    switch(code) {
        case 'auth/wrong-password':
            message = ' ';
            break;
        case 'auth/user-not-found':
            message = '     ';
            break;
    }

    return message;
}

export function convertResponse(responseValue) {
    const items = [];

    for (let [key, value] of Object.entries(responseValue)) {
        items.push({
            ...value,
            id: key
        });
    }

    return items;
}